import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import manufactorlogo from './SYM-BLK.png';

import indexStyles from './index.css';

class ShowcaseHeader extends React.Component {
 render() {
   return (
        <div className={indexStyles.showcase_header}>
           <div className={indexStyles.wc_cus_header}>
					  <NavLink to="/">
						<img alt="Showcase Symantec 2" src={manufactorlogo}/>
					  </NavLink>
			     </div>
        </div>
   );
 }
}

export default ShowcaseHeader;


